<template>
    <div class="user">
        <div class="info">
            <h1 class="title">个人信息</h1>
            <div class="info-detail">
                <div class="img">
                    <img :src="user1.avatar" />
                </div>
                <div class="info-container">
                    <ul class="detail-container">
                        <li v-for="(value, key) in user1" :key="key">
                            <p>
                                <span class="first">{{ key | user }}</span> :
                                <span
                                    class="last"
                                    v-if="typeof value === 'number'"
                                    >{{ value | date1 }}</span
                                >
                                <span v-else>
                                    {{ value }}
                                </span>
                            </p>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            user1: {}
        };
    },
    mounted() {
        this.getUser();
    },
    methods: {
        async getUser() {
            let id = this.$route.query.id;
            let views = await this.$store.dispatch("getUser", id);
            this.user1 = views;
        }
    }
};
</script>

<style lang="scss" scoped>
div.user {
    padding: 50px;
    div.info {
        width: 100%;
        height: 100%;
        padding: 0.5rem;
        box-sizing: border-box;
        position: relative;
        .upload {
            width: 0;
            height: 0;
            position: absolute;
            z-index: 0;
        }
        h1.title {
            font-weight: bold;
            font-size: 20px;
            height: 90px;
            line-height: 90px;
            border-bottom: 1px solid $uni-bg-color-grey-more;
        }

        div.info-detail {
            display: flex;
            flex-direction: row;
            margin-top: 1rem;
            flex-wrap: wrap;
            div.img {
                width: 100px;
                margin-right: 1rem;
                img {
                    width: 100px;
                    height: 100px;
                    border-radius: $uni-border-radius-circle;
                }
                p {
                    width: 100%;
                    color: $uni-color-primary;
                    text-align: center;
                    font-size: 14px;
                    margin-top: 8px;
                    cursor: pointer;
                    &:hover {
                        color: $uni-color-warning;
                    }
                }
            }

            div.info-container {
                width: 100%;
                padding: 0.5rem;
                box-sizing: border-box;
                ul.detail-container {
                    li {
                        font-size: 14px;
                        color: $uni-text-color;
                        line-height: 28px;
                        letter-spacing: 1px;
                        p {
                            span {
                                line-height: 22px;
                            }
                            // .first {
                            // }

                            // .last {
                            // }
                        }
                    }
                }
            }
        }
        ol,
        ul {
            list-style: none;
        }
    }
}
</style>